let initState = JSON.parse(localStorage.getItem("cart"))
  ? JSON.parse(localStorage.getItem("cart"))
  : [];

const Cart = (state = initState, action) => {
  switch (action.type) {
    case "addToCart":
      let isExistedIndex = state.findIndex((e) => e.id === action.payload.id);
      if (isExistedIndex >= 0) {
        state = initState;
        state.splice(isExistedIndex, 1, {...action.payload,
          quantity: state[isExistedIndex].quantity + action.payload.quantity,});
        localStorage.setItem("cart", JSON.stringify(state));
        return state;
      } else {
        state.push(action.payload);
        localStorage.setItem("cart", JSON.stringify(state));
        return state;
      }
    default:
      return state;
  }
};

export default Cart;
